.app {
	text-align: center;
}

@import url('https://fonts.googleapis.com/css?family=Gochi+Hand');

body {
	background-color: #494275;
	min-height: 70vh;
	margin-left: 30%;
	box-sizing: border-box;
	/* display: flex; */
	/* justify-content: center; */
	align-items: center;
	color: hsl(198, 1%, 29%);
	font-family: 'Gochi Hand', cursive;
	text-align: center;
	font-size: 130%;
}
@media only screen and (min-width: 500px) {
	body {
		min-height: 100vh;
	}
}

.container {
	width: 700px;
	height: auto;
	min-height: 300px;
	min-width: 250px;
	background: #f1f5f8;
	background-size: 25px 25px;
	border-radius: 20px;
	box-shadow: 4px 3px 7px 2px #00000040;
	box-sizing: border-box;
}

.form__label {
	display: block;
	margin-bottom: 0.5rem;
}
.form__input {
	box-sizing: border-box;
	background-color: transparent;
	padding: 0.7rem;
	border-bottom-right-radius: 15px 3px;
	border-bottom-left-radius: 3px 15px;
	border: solid 3px transparent;
	border-bottom: dashed 3px #323732;
	font-family: 'Gochi Hand', cursive;
	font-size: 1rem;
	color: hsla(127, 88%, 10%, 0.7);
	width: 70%;
	margin-bottom: 20px;
	&:focus {
		outline: none;
		border: solid 3px #ea95e0;
	}
}
@media only screen and (min-width: 500px) {
	.form__input {
		width: 60%;
	}
}

.button {
	margin-right: 10px;
	padding: 0;
	border: none;
	transform: rotate(4deg);
	transform-origin: center;
	font-family: 'Gochi Hand', cursive;
	text-decoration: none;
	padding-bottom: 3px;
	border-radius: 5px;
	box-shadow: 0 2px 0 hsl(198, 1%, 29%);
	transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
	background-color: hsla(167, 19%, 80%, 0.7);
}

.buttonDelete {
	padding: 0;
	border: none;
	transform: rotate(4deg);
	transform-origin: center;
	font-family: 'Gochi Hand', cursive;
	text-decoration: none;
	padding-bottom: 3px;
	border-radius: 5px;
	box-shadow: 0 2px 0 hsl(198, 1%, 29%);
	transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
	background-color: hsla(167, 19%, 80%, 0.7);
	float: right;
}

.buttonDivFilter {
	margin-top: 20px;
}

.buttonDivSearch {
	margin-top: 40px;
}

.buttonFilter {
	padding: 0;
	width: 100px;
	border: none;
	transform-origin: center;
	font-family: 'Gochi Hand', cursive;
	text-decoration: none;
	padding-bottom: 3px;
	border-radius: 5px;
	box-shadow: 0 2px 0 hsl(198, 1%, 29%);
	background-color: hsla(167, 19%, 80%, 0.7);
	float: left;
	margin-left: 10px;
}

.toDoList {
	text-align: left;
	color: #316829;
	margin-bottom: 5px;
	width: 300px;
	height: 30px;
	padding: 0 10px;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	box-shadow: 0 2px 0 hsl(198, 1%, 29%);
	background-color: hsla(167, 19%, 80%, 0.7);
}

:global .active {
	text-decoration: none;
}
